import React, {useContext} from 'react'
import { Button } from 'react-bootstrap';
import { default as MapStoreContext, MapStore } from '../../stores/mapstore';
import html2canvas from 'html2canvas'

const download = (image, fileName = 'screenshot.png') => {
    const a = document.createElement('a')
    a.href = image
    a.download = fileName
    a.click()
}

const screenshotMap = async (): Promise<any> => {
    const mapDom = document.getElementById('map')
    return mapDom == null 
        ? null
        : html2canvas(mapDom, {useCORS: true}).then(canvas => canvas.toDataURL('image/png'));
}

export const ScreenshotButton = () => {
    const mapStore: MapStore = useContext(MapStoreContext);
    return <Button variant="secondary border"
        onClick={() => {
            mapStore.hideControl()
            setTimeout(async () => {
                const screenshot = await screenshotMap()
                download(screenshot)
            }, 500)
            setTimeout(() => mapStore.showControl(), 1000)
        }}
    >
        <i className="bi bi-camera"></i>
    </Button>
}